import styled from 'styled-components'

import MediumFullButton from './MediumFullButton'

const FacebookButton = styled(MediumFullButton)`
  color: white;
  background-color: #4267b2;
  border: 1px solid #4267b2;
  font-weight: 400;
`

export default FacebookButton



